import React from "react";
import { TabData } from "./Tabs";

interface ITabDropdownProps {
  name: string;
  Tabs: TabData;
  onClick: (idx: number) => void;
}

export const TabDropdown = React.forwardRef((props: ITabDropdownProps, ref?: React.Ref<HTMLAnchorElement>) => {
  return (
    <div>
      <a
        id={"navBarDropdown"}
        className="nav-item nav-link dropdown dropdown-toggle"
        role="button"
        data-toggle="dropdown"
        aria-controls="nav-home"
        aria-selected="true"
        aria-haspopup="true"
        aria-expanded="false"
        data-tabindex="0"
        ref={ref}
      >
        {props.name}
      </a>
      <div className="dropdown-menu dropdown-menu-right">
        {props.Tabs.map(([name, path], idx) => {
          return <DropdownOption id={idx} key={name + idx} name={name} onClick={props.onClick.bind(path)} />;
        })}
      </div>
    </div>
  );
});

TabDropdown.displayName = "TabDropdown";

interface IDropdownOptionProps {
  id: number;
  name: string;
  onClick: (idx: number) => void;
}

class DropdownOption extends React.Component<IDropdownOptionProps> {
  constructor(props: IDropdownOptionProps) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.props.onClick(this.props.id);
  }

  render() {
    return (
      <a id={"dropdownOption" + this.props.id.toString()} className="dropdown-item" onClick={this.onClick}>
        {this.props.name}
      </a>
    );
  }
}
