import React from "react";
import ReactDOM from "react-dom";
import { ReadUrlParams, ExtractUrlParams, IMcParams } from "core-library";
import { NotebookViewController } from "./notebookViewController";
import { ErrorBoundary } from "./components/ErrorBoundary";
import { GetLessonData } from "./lessonData";
import { NotebookContentProvider } from "core-library";
import { AzureNotebookLoader } from "./azureNotebookLoader";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/js/dist/util";
import "bootstrap/js/dist/dropdown";
import "minecraft-styles/uwux-assets/css/mc_screen.css";
import "./index.scss";

const parameters: IMcParams = ExtractUrlParams();
console.log(`Starting Up Notebooks with the following paramters: ${JSON.stringify(parameters)}`);

ReactDOM.render(
  <ErrorBoundary>
    <NotebookViewController
      getLessonData={() => {
        return GetLessonData(parameters.lessonJsonPath, parameters.disableLessonDataCache);
      }}
      azureNotebookLoader={new AzureNotebookLoader()}
      contentProvider={new NotebookContentProvider(localStorage)}
      params={parameters}
    />
  </ErrorBoundary>
  ,
  document.getElementById("root")
);
