import React from "react";
import { NotebookParentElementId } from "../types";
import { getIpcTelemetry } from "../telemetry";

export interface INotebookProps {
  notebookUrl: string;
  activate: (url: string) => Promise<void>;
}

/**
 * log aznb errors
 *
 * @param error error to emit
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
function aznbErrorListener(error: Event) {
  getIpcTelemetry().error(error);
}

export class Notebook extends React.Component<INotebookProps> {
  constructor(props: INotebookProps) {
    super(props);
  }

  async componentDidMount() {
    const rootElement = document.getElementById(NotebookParentElementId);
    if (!rootElement) {
      console.warn(
        `HTMLElement with ID '${NotebookParentElementId}' does not exist. Telemetry of aznb-error events will be turned off.`
      );
    } else {
      rootElement.addEventListener("aznb-error", aznbErrorListener);
    }

    this.props.activate(this.props.notebookUrl);
  }

  async componentDidUpdate() {
    this.props.activate(this.props.notebookUrl);
  }

  componentWillUnmount() {
    const rootElement = document.getElementById(NotebookParentElementId);
    if (rootElement) {
      rootElement.removeEventListener("aznb-error", aznbErrorListener);
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  shouldComponentUpdate(_: INotebookProps, nextState: INotebookProps) {
    return _.notebookUrl !== this.props.notebookUrl;
  }

  render() {
    return (
      <div id="tab-content" className="window-tab">
        <div
          className="tab-pane fade show active"
          id={NotebookParentElementId}
          role="tabpanel"
        ></div>
      </div>
    );
  }
}
