// This file is for loading the lesson and notebook information.
// These functions will take in the lesson json file and load
// All the appropriate notebooks into tabs.

import { INotebookLoader, TelemetryEvents } from "./types";
import {
  ILessonData,
  NotebookContentProvider,
  IIpcTelemetry,
  MinecraftCommander,
  MockMinecraftCommander,
  IIpcRenderer,
  IActiveTabProvider,
  ActiveTabProvider,
  IActiveTab
} from "core-library";
import { IMcParams } from "core-library";
import React from "react";
import { INotebookTabsProps, TabData, Tabs } from "./components/Tabs";
import { Notebook } from "./components/Notebook";
import { Locale } from "extension-minecraft/lib/types";
import { GetLessonDataFunctor } from "./lessonData";
import { getIpcTelemetry } from "./telemetry";
import { FooterButtons } from "./components/FooterButtons";

export type PageId = string;
export type PageName = string;
export type NotebookRelativePath = string;
export type IsLocked = boolean;

declare const window: {
  ipcRenderer: IIpcRenderer | undefined;
};

export interface INotebookControllerProps {
  getLessonData: GetLessonDataFunctor;
  azureNotebookLoader: INotebookLoader;
  contentProvider: NotebookContentProvider;
  params: IMcParams;
}

interface INotebookControllerState {
  // Since we start initalization after mounting. Flag to indicate loading has completed.
  hasLoaded: boolean;
  tabProps: INotebookTabsProps;
}

/*
    ViewController that handles entrypoint into out app. Similar to App.tsx.
    We are currently not using any off the shelf controller, since we just need one.

    Current App lifecycle:
    - Constructor - Sets up post mounting initalization functor. The functor does the following:
        - Fetches the lessonData (lesson.json)
        - Iniitalizes the Azure Notebook Components.
        - Parses the lesson.json and downloads all notebooks.
    - Render - Either renders a loading acreen or Actual Tabs/Notebook after it is available.
    - OnComponentMounted  - Begins executing the above functor.
*/
export class NotebookViewController extends React.Component<INotebookControllerProps, INotebookControllerState> {
  // LessonData is populated after the Component has mounted.
  private LessonData?: ILessonData;
  private TelemetryProvider?: IIpcTelemetry;
  private ActiveTabProvider?: IActiveTabProvider;

  constructor(props: INotebookControllerProps) {
    super(props);

    this.state = {
      hasLoaded: false,
      tabProps: {
        tabs: [],
        activeTabPath: ""
      }
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } as any;

    this.onActivate = this.onActivate.bind(this);
    this.onActivateByIndex = this.onActivateByIndex.bind(this);
  }

  // Start initalization after the initial app frame has loaded. Minecraft depends on scripts
  // being injected, and each platform has a different timing. The best time would be after the
  // component has moundtes.
  async componentDidMount() {
    this.ActiveTabProvider = new ActiveTabProvider(
      localStorage,
      window?.ipcRenderer ? MinecraftCommander.getInstance(window.ipcRenderer) : new MockMinecraftCommander()
    );
    this.TelemetryProvider = getIpcTelemetry();

    let component = "";
    const initEvent: TelemetryEvents.Components = { components: [] };

    try {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      const telemetry = async (comp: string, func: () => any | undefined) => {
        component = comp;
        const startTime = performance.now();
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const details: any = await func();
        const timeElapsed = Math.trunc(performance.now() - startTime);
        initEvent.components.push({
          id: component,
          timeElapsed: timeElapsed,
          details: details
        } as TelemetryEvents.Component);
      };

      await telemetry("getLessonData", async () => {
        this.LessonData = await this.props.getLessonData();
        this.TelemetryProvider?.setName(`Everglade:${this.LessonData.creatorName}:${this.LessonData.title}`);
      });

      await telemetry("aznb::initalize", async () => {
        await this.props.azureNotebookLoader.initalize(this.props.params.language as Locale);
      });

      await telemetry("loadNotebooks", async () => {
        const tabProps: INotebookTabsProps = await this.load();
        this.setState({ hasLoaded: true, tabProps: tabProps });
        return { notebookCount: tabProps.tabs?.length };
      });
    } catch (error) {
      const e: Error = error;
      this.TelemetryProvider?.error({
        component: component,
        message: e.message
      });
      this.setState(() => {
        throw e;  
      });
      throw error;
    } finally {
      this.TelemetryProvider?.log(initEvent);
    }
  }

  // Gets the path of a notebook based on supported languages.
  private getNotebookPath(filename: string): string {
    // When we go offline, we will need to fix this seperator based. Maybe the filesystem will ignore it.
    const seperator = "/";

    // en-us is the default language and shouldn't have its own folder.
    if (
      this.props.params.language &&
      this.LessonData &&
      this.LessonData.isLanguageSupported(this.props.params.language, this.LessonData.additionalLanguages)
    ) {
      return (
        this.props.params.rootNotebookPath + seperator + this.props.params.language + seperator + filename + ".ipynb"
      );
    } else {
      return this.props.params.rootNotebookPath + filename + ".ipynb";
    }
  }

  private async load(): Promise<INotebookTabsProps> {
    if (this.LessonData) {
      const tabIndexFunctor = this.ActiveTabProvider?.get(
        this.props.params.lessonJsonPath,
        this.LessonData?.activityTracker?.data?.activitySelectorObjective,
        (data: IActiveTab) => {
          // Ignore calls that occur before loading is complete.
          if (
            this.state.hasLoaded &&
            this.state.tabProps?.tabs &&
            data.activeTabIndex > 0 &&
            // eslint-disable-next-line @typescript-eslint/no-use-before-define
            data.activeTabIndex < tabs.length
          ) {
            this.onActivate(this.state.tabProps.tabs[data.activeTabIndex][1], data.activeTabIndex);
          }
        }
      ).then((activeTab) => {
        return activeTab ? activeTab : ({ activeTabIndex: 0 } as IActiveTab);
      });

      const pagePaths = await Promise.all(
        this.LessonData.pages.map(async (page) => {
          const path = this.getNotebookPath(page);
          try {
            const notebook = await this.props.contentProvider.get(path, true);
            // TODO: Validate Metadata Minecraft
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            const metadata = notebook.metadata as any;
            return [metadata.minecraft.page_name, path] as [PageName, NotebookRelativePath];
          } catch (error) {
            const e = new Error("Notebook could not be loaded from path: " + path);
            throw e;
          }

        })
      );

      const tabIndex = await tabIndexFunctor;
      const tabs: TabData = [];
      for (const [name, path] of pagePaths) {
        tabs.push([name, path, false]);
      }

      const index =
        tabIndex !== undefined && tabIndex.activeTabIndex >= 0 && tabIndex.activeTabIndex < tabs.length
          ? tabIndex.activeTabIndex
          : 0;

      return {
        tabs,
        activeTabPath: pagePaths[index][1],
        activeTabIndex: index
      } as INotebookTabsProps;
    } else {
      throw new Error("Notebook Controller Loading failed: LessonData cant be empty.");
    }
  }

  onActivate(url: string, idx: number) {
    const tabProps = this.state.tabProps;
    tabProps.activeTabPath = url;
    tabProps.activeTabIndex = idx;
    this.setState({ hasLoaded: true, tabProps: tabProps });
  }

  onActivateByIndex(idx: number) {
    this.onActivate(this.getTabPathFromIndex(idx), idx);
  }

  getTabPathFromIndex(index: number): string {
    let path = "";
    for (let idx = 0; idx < this.state.tabProps.tabs.length; idx++) {
      if (index === idx) {
        path = this.state.tabProps.tabs[idx][1];
      }
    }
    return path;
  }

  public render() {
    if (!this.state.hasLoaded) {
      return (
        <span className="page-loader">
          <img />
        </span>
      );
    } else {
      this.TelemetryProvider?.setContext("loaded");
      return (
        <div id="page" className="pageBody">
          <div id="body">
            <Tabs
              tabs={this.state.tabProps.tabs}
              activeTabPath={this.state.tabProps.activeTabPath}
              activeTabIndex={this.state.tabProps.activeTabIndex}
              onActivate={this.onActivate}
            />

            <div className="scroll-pane-wrapper">
              <div id="#scroll-pane" className="scroll-target">
                <Notebook
                  notebookUrl={this.state.tabProps.activeTabPath}
                  activate={async (url: string) => {
                    return this.props.azureNotebookLoader.loadComponent(url);
                  }}
                />
                <FooterButtons
                  previousButton={
                    this.state.tabProps.activeTabIndex == 0
                      ? undefined
                      : { onClick: this.onActivateByIndex.bind(this, this.state.tabProps.activeTabIndex - 1) }
                  }
                  nextButton={
                    this.state.tabProps.activeTabIndex == this.state.tabProps.tabs.length - 1
                      ? undefined
                      : { onClick: this.onActivateByIndex.bind(this, this.state.tabProps.activeTabIndex + 1) }
                  }
                />
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}
