import React from "react";
import { PageName, NotebookRelativePath } from "../notebookViewController";
import { Tab } from "./Tab";
import { TabDropdown } from "./TabDropdown";
import { TabArrow } from "./TabArrow";
import { IActiveTab } from "core-library";

export type IsLocked = boolean;
export type TabData = Array<[PageName, NotebookRelativePath, IsLocked]>;

type IsShown = boolean;
type Width = number;

export interface INotebookTabsProps extends IActiveTab {
  tabs: TabData;
  activeTabPath: string;
  onActivate: (url: string, idx: number) => void;
}

interface INotebookTabsState {
  showNavigationTabs: boolean;
  navBarStartIndex: number;
  navBarEndIndex: number;
  navBarTotalWidth: number;
}

/*
    Implements the Tabs Panel which includes the following:
    1) Navigation Components (Left Arrow, Right Arrow and drop down)
        - If requried all 3 components are shown.
    2) List of tabs which activate a page when clicked.
        - Tabs that don't fit on the clients width are hidden. You can use the nav components to travers them.
*/
export class Tabs extends React.Component<INotebookTabsProps, INotebookTabsState> {
  // Navigation Components
  tabContainer: [React.RefObject<HTMLDivElement>, Width];
  leftArrow: [React.RefObject<HTMLAnchorElement>, Width];
  rightArrow: [React.RefObject<HTMLAnchorElement>, Width];
  dropDown: [React.RefObject<HTMLAnchorElement>, Width];
  tabs: Array<[IsShown, React.RefObject<HTMLAnchorElement>, Width, string, () => void]>;

  resizeTimeout: number | undefined;

  constructor(props: INotebookTabsProps) {
    super(props);
    this.select = this.select.bind(this);
    this.onActivate = this.onActivate.bind(this);
    this.resizeCallback = this.resizeCallback.bind(this);

    // Populate tabs
    this.tabs = new Array<[IsShown, React.RefObject<HTMLAnchorElement>, Width, string, () => void]>();

    const leftArrowRef: React.RefObject<HTMLAnchorElement> = React.createRef();
    this.leftArrow = [leftArrowRef, 0];

    this.props.tabs.forEach(([name, path], idx) => {
      const ref: React.RefObject<HTMLAnchorElement> = React.createRef();
      this.tabs.push([true, ref, 0, name, this.onActivate.bind(this, path, idx)]);
    });

    this.rightArrow = [React.createRef(), 0];
    this.dropDown = [React.createRef(), 0];
    this.tabContainer = [React.createRef(), 0];

    this.state = {
      showNavigationTabs: true,
      navBarStartIndex: 0,
      navBarEndIndex: this.tabs.length,
      anchorRight: false,
      navBarTotalWidth: 0
    } as INotebookTabsState;
  }

  onActivate(url: string, idx: number): void {
    this.props.onActivate(url, idx);
  }

  // Handle selecting a tab on the panel. When a dropdown is selected.
  select(tabIndex: number) {
    // Index is not in range.
    if (tabIndex < 0 || tabIndex >= this.props.tabs.length) {
      return;
    }

    if (tabIndex < this.state.navBarStartIndex || tabIndex > this.state.navBarEndIndex) {
      this.repopulate(tabIndex);
    }

    // Begin activation process
    this.onActivate(this.props.tabs[tabIndex][1], tabIndex);
  }

  componentDidMount() {
    window.addEventListener("resize", this.resizeCallback);
    this.initalizeWidths();
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  componentDidUpdate(prevProps: INotebookTabsProps, prevState: INotebookTabsState) {
    // In case the index is not in the current display, call select
    if (
      prevProps.activeTabIndex != this.props.activeTabIndex ||
      this.props.activeTabIndex > this.state.navBarEndIndex
    ) {
      this.select(this.props.activeTabIndex);
    }
  }

  initalizeWidths() {
    // Get lengths of Navbar compoenents (left arrow, rigth arrow and drop down)
    this.leftArrow[1] = this.leftArrow[0].current?.offsetWidth ?? 0;
    this.rightArrow[1] = this.rightArrow[0].current?.offsetWidth ?? 0;
    this.dropDown[1] = this.dropDown[0].current?.offsetWidth ?? 0;
    this.tabContainer[1] = this.tabContainer[0].current?.offsetWidth ?? 0;

    // Start Total width with arrow lengths
    let totalWidth = this.leftArrow[1] + this.rightArrow[1] + this.dropDown[1] + 30;

    let shouldDisplay = true;
    let updateNavBarEndIndex: number = this.tabs.length - 1;
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    this.tabs = this.tabs.map(([isShown, ref, width, name, onclick], idx) => {
      const currentWidth = ref.current?.offsetWidth ?? 0;
      totalWidth += currentWidth;
      if (shouldDisplay && totalWidth > this.tabContainer[1]) {
        // Navbar Index ends here. Use the previous index
        updateNavBarEndIndex = idx - 1;
        shouldDisplay = false;
      }

      return [shouldDisplay, ref, currentWidth, name, onclick];
    });

    this.setState({
      showNavigationTabs: !shouldDisplay,
      navBarEndIndex: updateNavBarEndIndex,
      navBarTotalWidth: totalWidth
    });
  }

  // Tab Panel:  | < | Tab1 | Tab2 | *Active Tab3 *| > | ... |
  // An active tab on the right most side of the tab panel is anchored/stuck to the corner.
  // Resizing or clicking on the right most arrow will make sure that it is always active in the
  // same spot.
  repopulateRightAnchored(tabIndex: number, totalWidth: number) {
    let shouldDisplay = false;
    // We iterate through the tabs from the other end (reverse map).
    for (let idx = this.tabs.length - 1; idx >= 0; idx--) {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      const [isShown, ref, width, name, onclick] = this.tabs[idx];

      if (idx == tabIndex) {
        shouldDisplay = true;
      } else if (idx > tabIndex) {
        shouldDisplay = false;
      }

      if (shouldDisplay) {
        totalWidth += width;
        if (totalWidth > (this.tabContainer[1] ?? 0)) {
          // Looks like we have run out of space, lets use the last index we came across (in revesre so +)
          this.setState({
            navBarStartIndex: idx + 1,
            navBarEndIndex: tabIndex,
            showNavigationTabs: true
          });
          shouldDisplay = false;
        }
      }

      this.tabs[idx] = [shouldDisplay, ref, width, name, onclick];
    }
  }

  repopulateLeftAnchored(tabIndex: number, totalWidth: number, isResize: boolean) {
    let shouldDisplay = false;
    for (let idx = 0; idx < this.tabs.length; idx++){
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      const [isShown, ref, width, name, onclick] = this.tabs[idx];
      if (idx == tabIndex) {
        shouldDisplay = true;
      } else if (isResize && idx < tabIndex) {
        shouldDisplay = false;
      }

      if (shouldDisplay) {
        totalWidth += width;

        if (totalWidth > (this.tabContainer[1] ?? 0)) {
          // Looks like we are out of space, use the last index that worked.
          this.setState({
            navBarStartIndex: tabIndex,
            navBarEndIndex: idx - 1,
            showNavigationTabs: true
          });
          shouldDisplay = false;
        }
      }

      this.tabs[idx] = [shouldDisplay, ref, width, name, onclick];
    }
  }

  repopulate(tabIndex: number) {
    // track the allocated pixel width
    const totalWidth = this.leftArrow[1] + this.rightArrow[1] + this.dropDown[1] + 30;

    if (
      (this.tabContainer[0].current?.clientWidth &&
      this.tabContainer[1] == this.tabContainer[0].current.clientWidth && // ref's are loaded, and we aren't in the middle of a resize
      tabIndex >= 0 &&
      tabIndex < this.props.tabs.length && // Is not an incorrect index value
        tabIndex < this.state.navBarStartIndex) ||
      tabIndex > this.state.navBarEndIndex
    ) {
      // Check if tabs need an update.

      // Get the base width, with some padding.
      if (tabIndex > this.state.navBarEndIndex) {
        this.repopulateRightAnchored(tabIndex, totalWidth);
      } else {
        this.repopulateLeftAnchored(tabIndex, totalWidth, false);
      }
    } else if (
      this.tabContainer[0].current?.clientWidth &&
      this.tabContainer[1] != this.tabContainer[0].current.clientWidth
    ) {
      // Reset the tab container width to the new size.
      const shouldUseRightAnchored = tabIndex == this.state.navBarEndIndex && this.state.navBarStartIndex != 0;
      this.tabContainer[1] = this.tabContainer[0].current.clientWidth;

      if (this.state.navBarTotalWidth > this.tabContainer[1]) {
        // Looks like we need to resize
        if (shouldUseRightAnchored) {
          this.repopulateRightAnchored(tabIndex, totalWidth);
        } else {
          this.repopulateLeftAnchored(tabIndex, totalWidth, true);
        }
      }

      this.forceUpdate();
    }
  }

  resizeCallback() {
    // Everytime reset is called we resize. If this gets too expensive
    // we should "debouce" this. Use setIntercal with a small timeout (100 ms).
    clearTimeout(this.resizeTimeout);
    this.resizeTimeout = setTimeout(this.repopulate.bind(this, this.props.activeTabIndex) as TimerHandler, 200);
  }

  render() {
    return (
      <div id="tabContainer" ref={this.tabContainer[0]}>
        <nav>
          <div id="nav-tabs" className="nav nav-tabs tabs-sm dark-tabs window-tabs" role="tablist">
            {this.state.showNavigationTabs ? (
              <TabArrow
                id={"left"}
                key="leftArrow"
                isDisabled={this.props.activeTabIndex == 0}
                isLeftArrow={true}
                onClick={this.select.bind(this, this.props.activeTabIndex - 1)}
                ref={this.leftArrow[0]}
              ></TabArrow>
            ) : null}
            {this.tabs.map(([isShown, ref, , name, onClick], idx) => {
              if (isShown) {
                return (
                  <Tab
                    id={idx}
                    name={name}
                    key={idx}
                    isActive={idx == this.props.activeTabIndex}
                    onClick={onClick}
                    ref={ref}
                  />
                );
              }
            })}
            {this.state.showNavigationTabs ? (
              <TabArrow
                id={"right"}
                key="rightArrow"
                isDisabled={this.props.activeTabIndex == this.props.tabs.length - 1}
                isLeftArrow={false}
                onClick={this.select.bind(this, this.props.activeTabIndex + 1)}
                ref={this.rightArrow[0]}
              ></TabArrow>
            ) : null}
            {this.state.showNavigationTabs ? (
              <TabDropdown
                name="..."
                key="tabDropdown"
                onClick={this.select.bind(this)}
                Tabs={this.props.tabs}
                ref={this.dropDown[0]}
              />
            ) : null}
          </div>
        </nav>
      </div>
    );
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resizeCallback);
  }
}
