import { INotebookLoader, NotebookParentElementId } from "./types";
import AzureNotebooks from "@aznb/extension-contract";
import MinecraftExtension, { mapLocaleToAznbLocale, Locale } from "extension-minecraft";
import { IIpcTelemetry } from "core-library";
import { getIpcTelemetry } from "./telemetry";

// Load Notebook on specified id
export class AzureNotebookLoader implements INotebookLoader {
  private isInitalized: boolean;
  private rootElementId: string;
  private telemetryLogger: IIpcTelemetry;
  private locale: Locale | undefined;

  constructor(rootElementId: string = NotebookParentElementId) {
    this.rootElementId = rootElementId;
    this.isInitalized = false;

    this.telemetryLogger = getIpcTelemetry();
  }

  public async initalize(language: Locale = "en-us"): Promise<void> {
    this.locale = language;
    MinecraftExtension.settings.locale = language;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    AzureNotebooks.registerExtension(MinecraftExtension as any);

    return AzureNotebooks.init({
      userHasGivenCookieConsent: false,
      lazyLoadEditor: false,
      getResourceIdAndKey: () =>
        Promise.resolve({
          notebookResourceId: "",
          notebookResourceKey: ""
        })
    }).then(() => {
      this.isInitalized = true;
    });
  }

  public async loadComponent(filepath: string): Promise<void> {
    if (!this.isInitalized) {
      throw new Error("Initalize needs to be called before proceeding.");
    }

    const nb = await AzureNotebooks.loadNotebook({
      parentElementId: this.rootElementId,
      filePath: filepath, //BUG: filepath must be a url
      config: {
        disableOutputIframe: true,
        locale: mapLocaleToAznbLocale(this.locale)
      },
      extensions: ["minecraft"]
    });

    // This token info is overridden by the extension to use the ipc protocol from Minecraft
    // Example compute setting: local jupyter host with token auth
    const compute = {
      name: "Local",
      targetUrl: "http://localhost:8888",
      getAuthSettings: () =>
        Promise.resolve({
          authType: "token",
          token: "token to access your local jupyter host"
        }),
      shouldMount: false
    };

    await nb.setCompute({ targetSettings: compute });
  }
}
