import React from "react";

interface IFooterButton {
  onClick: () => void;
}

interface IFooterButtonsProps {
    previousButton?: IFooterButton;
    nextButton?: IFooterButton;
}

export class FooterButtons extends React.Component<IFooterButtonsProps> {
  constructor(props: IFooterButtonsProps) {
    super(props);
  }

  render() {
    const classes = "footer-button __ButtonLiteGrey arrow-nav";
    const classesLeft = classes + " arrow-nav-left";
    const classesRight = classes + " arrow-nav-right";

    return (
      <div id="footerButtons" className="footer_controls">
        <button
          id={"footerButtonPrevious"}
          className={classesLeft + (this.props.previousButton ? "" : " disabled")}
          disabled={this.props.previousButton == undefined}
          onClick={this.props.previousButton?.onClick}
        >
          <i className="icon icon_chevron-down" aria-hidden="true"></i>
        </button>
        <button
          id={"footerButtonNext"}
          className={classesRight + (this.props.nextButton ? "" : " disabled")}
          disabled={this.props.nextButton == undefined}
          onClick={this.props.nextButton?.onClick}
        >
          <i className="icon icon_chevron-down" aria-hidden="true"></i>
        </button>
      </div>
    );
  }
}
