import { ILessonData, AdditionalLanguages, ValidateLessonData } from "core-library";

/**
 * Check if language is supported
 *
 * @param lang - language to check
 * @param additionalLanguages - language packs to look for the language
 */
function defaultIsLanguageSupported(lang: string, additionalLanguages: AdditionalLanguages) {
  return !!additionalLanguages && additionalLanguages.includes(lang);
}

export type GetLessonDataFunctor = () => Promise<ILessonData>;

/**
 * Retrieves the lesson.json from the given url.
 * We currently don't cache the lesson.json.
 *
 * @param url - url
 */
export async function GetLessonData(url: string, bypassCache?: boolean): Promise<ILessonData> {
  const httpOptions: RequestInit = {
    method: "GET",
    headers: Object.create(null),
    mode: "cors",
  };

  if (bypassCache) {
    httpOptions.cache = "no-store";
  }

  return fetch(url, httpOptions)
    .then((response: Response) => {
      return response.json();
    })
    .then((json: ILessonData) => {
      if (ValidateLessonData(json)) {
        json.isLanguageSupported = defaultIsLanguageSupported;
        return json;
      } else {
        throw new Error("Invalid LessonData. title, description and pages are all required values.");
      }
    })
    .catch((error) => {
      console.error(`Failed to get lesson data: ${error}`);
      throw error;
    });
}
