import React from "react";
import { IIpcTelemetry } from "core-library";
import { getIpcTelemetry } from "../telemetry";
import { getLocStrings } from "../localization";

interface IErrorBoundaryState {
  hasError: boolean;
  errorMessage: string;
}

export class ErrorBoundary extends React.Component<{}, IErrorBoundaryState> {
  private telemetryLogger: IIpcTelemetry;

  constructor(props = {}) {
    super(props);
    this.state = { hasError: false, errorMessage: "" };
    this.telemetryLogger = getIpcTelemetry();
  }

  static getDerivedStateFromError(error: Error) {
    return { hasError: true, errorMessage: error.message };
  }

  componentDidCatch(error: Error) {
    this.telemetryLogger.error(error);
  }

  render() {
    if (this.state.hasError) {
      const locStrings = getLocStrings();

      return (
        <div>
          <h1 id="error-boundary-header" style={{color:"white"}}>{locStrings.ErrorBoundaryMessage}</h1>
          <p id="error-boundary-message" style={{color:"white"}}>{locStrings.ErrorInfoPrefix + this.state.errorMessage}</p>
        </div>
      );
    }

    return this.props.children;
  }
}