import { MockIpcTelemetry, IIpcTelemetry } from "core-library";

declare const window: { ipcTelemetry: IIpcTelemetry | undefined };

/**
 * Create an instance of ipcTelemetry
 */
function initIpcTelemetry() {
  return window.ipcTelemetry || (new MockIpcTelemetry() as IIpcTelemetry);
}

/** A singleton instance of ipcTelemetry */
let ipcTelemetry: IIpcTelemetry | undefined;

/**
 * Get instance of ipcTelemetry
 */
export function getIpcTelemetry() {
  if (ipcTelemetry === undefined) {
    ipcTelemetry = initIpcTelemetry();
    ipcTelemetry.register("Everglade", location.href, "initialization");
  }

  return ipcTelemetry;
}
