import en from "./en";
import { settings } from "../evergladeSettings";
import LocalizedStrings from "react-localization";
import zhCn from "./zh-cn";

type ILanguageStrings = typeof en;

// Append "PS_" prefix to every string to create a pesudo language pack
const createPesudoStrings = (origin: ILanguageStrings): ILanguageStrings => {
  const result = {} as ILanguageStrings;
  Object.keys(origin).forEach((k: keyof ILanguageStrings) => {
    result[k] = "PS_" + origin[k];
  });

  return result;
};

const ps = createPesudoStrings(en);

const strings = new LocalizedStrings({
  "en-us": en,
  "ps-ps": ps,
  "zh-cn": zhCn
});

strings.setLanguage("en-us");

export const getLocStrings = () => {
  if (settings.locale !== strings.getLanguage()) {
    strings.setLanguage(settings.locale);
  }
  return strings;
};
