import React from "react";
import { PageName } from "../notebookViewController";

export interface ITabProps {
  id: number;
  name: PageName;
  isActive?: boolean;
  isDisabled?: boolean;
  onClick: () => void;
}

export const Tab = React.forwardRef((props: ITabProps, ref?: React.Ref<HTMLAnchorElement>) => {
  let myClassName = "nav-item nav-link";
  if (props.isActive) {
    myClassName += " active";
  }

  return (
    <a
      id={"tab" + props.id.toString()}
      className={myClassName}
      role="tab"
      data-toggle="tab"
      aria-controls="nav-home"
      aria-selected="true"
      data-tabindex="0"
      onClick={props.onClick}
      ref={ref}
    >
      {props.name}
    </a>
  );
});

Tab.displayName = "Tab";
