import React from "react";

export interface ITabArrowProps {
  id: string;
  isDisabled: boolean;
  isLeftArrow: boolean;
  onClick: () => void;
}

export const TabArrow = React.forwardRef((props: ITabArrowProps, ref?: React.Ref<HTMLAnchorElement>) => {
  let classId = "nav-item nav-link arrow-nav" + (props.isLeftArrow ? " arrow-nav-left" : " arrow-nav-right");
  if (props.isDisabled) {
    classId += " disabled";
  }

  return (
    <a
      id={"arrow-" + props.id}
      className={classId}
      role="tab"
      aria-controls="nav-home"
      aria-selected="true"
      data-tabindex="0"
      onClick={props.isDisabled ? undefined : props.onClick}
      ref={ref}
    ></a>
  );
});

TabArrow.displayName = "TabArrow";
