/* eslint-disable @typescript-eslint/no-namespace */
import { Locale } from "extension-minecraft/lib/types";

// Minecraft Interface for communicating.
// eslint-disable-next-line @typescript-eslint/interface-name-prefix
export interface IpcRenderer {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  sendToHost(event: string, body: any): void;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  on(event: string, body: any): void;
}

export interface ITabData {
  id: number; // Not sure why we need this. Maps to page_id under metadata
  title: string; // Contains the title. This should have a size limit.
}

export const NotebookParentElementId = "notebook-container";

export interface INotebookLoader {
  loadComponent: (filepath: string) => Promise<void>;
  initalize(language: Locale): Promise<void>;
}

export declare namespace TelemetryEvents {
  // eslint-disable-next-line @typescript-eslint/interface-name-prefix
  export interface Component {
    id: string;
    timeElapsed?: number;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    details?: any;
  }

  // eslint-disable-next-line @typescript-eslint/interface-name-prefix
  export interface Components {
    components: Component[];
  }
}

export interface ISettings {
  logToConsole: boolean;
  locale: Locale;
}
